.sidebar {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 59px;
  height: 138px;
  background-color: rgba(201, 194, 175, 0.51);
  border-radius: 10px;
  left: 120px;
  top: 499px;
  z-index: 99;
}

.frame {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 10px;
}

.frame:not(:last-child) {
  margin-bottom: 10px;
}

.frame:hover {
  background-color: #c5c2c2;
}

.frame:active {
  background-color: rgba(0, 0, 0, 0.2);
}

.icon {
  color: #555555;
  width: 36px;
  height: 36px;
}

.icon:active {
  color: #ffffff;
}
